@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans&display=swap);
/*--- Variables ---*/
#root {
  font-family: "IBM Plex Sans", sans-serif;
  padding: 0;
  margin: 0;
  color: #1f2129;
  font-size: 12px; }

.wrapper {
  width: '100%';
  margin-top: 5rem;
  overflow-x: 'auto'; }

.MuiTable-root {
  max-width: 1440px;
  margin: auto;
  border: #E4E7F0 1px solid; }

.MuiTableCell-root {
  font-size: 13px;
  padding: 13px 10px; }
  .MuiTableBody-root .MuiTableCell-root.name, .MuiTableBody-root .MuiTableCell-root.email {
    color: #356ae6; }
  .MuiTableBody-root .MuiTableCell-root.name {
    font-weight: 600; }
  .MuiTableCell-root.MuiTableCell-head {
    padding: 4px 10px; }

.context-header {
  display: flex;
  align-items: center;
  padding-left: 1rem;
  height: 64px;
  max-height: 64px;
  background-color: #FAFAFA;
  box-shadow: inset 0 -1px 0 0 #CACACA; }

.text-left {
  text-align: left; }

.MuiTableHead-root {
  height: 28px !important; }

